,wDACA,yBACA,gBACA,iBAAmB,CALvB,oBAOQ,WACA,YACA,uDACA,0BACA,mBACA,gBACA,UAAY,CAbpB,wBAeY,WACA,YACA,aAAe,CAjB3B,iBAqBQ,iBACA,gBACA,UAAY,CAvBpB,aA0BQ,WACA,gBACA,mBACA,uBACA,eACA,iBACA,gBAAkB,CAhC1B,kBAkCY,gBAAkB,CAlC9B,iBAsCQ,cACA,wDACA,uBAAyB,CAC5B,UAID,gBACA,WACA,yBACA,kBACA,YAAc,CALlB,iBAOQ,YACA,aACA,kBACA,gBACA,oBAAsB,CAX9B,kBAcQ,UAAY,CAdpB,oBAiBQ,yBACA,WACA,eAAiB,CAnBzB,4BAqBY,UACA,WACA,iBAAkB,CAvB9B,6BA0BY,UACA,WACA,iBACA,mBACA,aLpGI,CKqGP,SAKL,sBACA,WACA,CACH,oBADG,+BLhHY,CKmHhB,AAFC,WAGG,WACA,YACA,gBACA,AACA,iBAAmB,CALvB,iBAOQ,UACA,iBACA,WACA,aACA,kBACA,eAAkB,CAZ1B,iBAeQ,eACA,cACA,kBACA,UACA,UACA,eAAkB,CApB1B,eAuBQ,8BL1IQ,CKmHhB,qBA0BQ,cACA,+BL5II,CK6IP,YAID,gBACA,cACA,aAAe,CAClB,MC3JG,sBACA,YACA,cAAiB,CAHrB,iBAMQ,gBACA,yBACA,yBACA,6BACA,+BNJQ,CMNhB,kBAaQ,WACA,eAAgB,CAdxB,qBAgBY,WACA,gBACA,cAAgB,CAlB5B,wBAoBgB,WACA,gBACA,+BNhBA,CMNhB,mCAwBoB,kBAAoB,CAxBxC,8BA2BoB,aNjBJ,CMVhB,8BA8BoB,cAAgB,CA9BpC,qBAoCQ,aAAc,CApCtB,aAuCQ,WACA,gBACA,4BNnCQ,CMNhB,mBA2CY,eACA,UACA,qBACA,UAAY,CA9CxB,kBAiDY,qBACA,kBACA,kBACA,eACA,iBACA,UACA,yBACA,eACA,UNvDE,CMFd,kBA4DY,gBAAkB,CA5D9B,eAiEQ,cAAiB,CAjEzB,2BAgEQ,+BACA,CAjER,YAoEQ,WACA,YACA,gBACA,gBACA,AACA,iBAAmB,CAzE3B,kBA2EY,UACA,iBACA,WACA,aACA,kBACA,eAAkB,CAhF9B,kBAoFY,eACA,cACA,kBACA,UACA,UACA,eAAiB,CAzF7B,gBA4FY,8BNtFI,CMNhB,sBA+FY,cACA,+BNxFA,CMRZ,kBAoGQ,gBACA,aAAe,CAClB","file":"CV.scss","sourcesContent":["/**\n * Global Reset of all HTML Elements\n *\n * Resetting all of our HTML Elements ensures a smoother\n * visual transition between browsers. If you don't believe me,\n * try temporarily commenting out this block of code, then go\n * and look at Mozilla versus Safari, both good browsers with\n * a good implementation of CSS. The thing is, all browser CSS\n * defaults are different and at the end of the day if visual\n * consistency is what we're shooting for, then we need to\n * make sure we're resetting all spacing elements.\n *\n */\nhtml, body {\n    height: 100%;\n    border: 0;\n    font-family: \"Helvetica-Neue\", \"Helvetica\", Arial, sans-serif;\n    line-height: 1.5;\n    margin: 0;\n    padding: 0;\n}\n\ndiv, span, object, iframe, img, table, caption, thead, tbody,\ntfoot, tr, tr, td, article, aside, canvas, details, figure, hgroup, menu,\nnav, footer, header, section, summary, mark, audio, video {\n    border: 0;\n    margin: 0;\n    padding: 0;\n}\n\nh1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, address, cit, code,\ndel, dfn, em, ins, q, samp, small, strong, sub, sup, b, i, hr, dl, dt, dd,\nol, ul, li, fieldset, legend, label {\n    border: 0;\n    font-size: 100%;\n    vertical-align: baseline;\n    margin: 0;\n    padding: 0;\n}\n\narticle, aside, canvas, figure, figure img, figcaption, hgroup,\nfooter, header, nav, section, audio, video {\n    display: block;\n}\n\ntable {\n    border-collapse: separate;\n    border-spacing: 0;\n    caption, th, td {\n        text-align: left;\n        vertical-align: middle;\n    }\n}\nli{\n    list-style: none;\n}\na{\n    text-decoration: none;\n}\na img {\n    border: 0;\n}\n\n:focus {\n    outline: 0;\n}\n*{\n    box-sizing: border-box;\n}\ntextarea{\n    resize: none;\n    appearance: none;\n}\ninput{\n    appearance: none;\n}\nselect {\n    appearance: none;\n    background-color: #fff;\n}\n","$color31:#313131;\n$colorf0:#f0f0f0;\n$colorfff:#fff;\n$colore7:#e7e7e7;\n$colorfa:#fafafa;\n$color62:#626262;\n$colord4:#d4d4d4;\n$colora0:#a0a0a0;\n$red:#ff5a5f;\n$yellow:#feb501;\n$color80:#80bd01;\n$gap:5px;\n\nbody {\n    height: 100%;\n    width: 100%;\n    font-size: 14px;\n    color: #313131;\n    overflow-x: hidden;\n    line-height: 1;\n    background: url('../images/loading.gif') no-repeat center 150px;\n}\n\n.mt5 {\n    margin-top: $gap;\n}\n\n.mt10 {\n    margin-top: $gap*2;\n}\n\n.color80 {\n    color: #42b983;\n}\n\n.clear {\n    zoom: 1;\n    &:after {\n        content: '';\n        display: table;\n        clear: both;\n    }\n}\n\n#app {\n    height: 100%;\n}\n\n.display-flex {\n    display: -moz-box;\n    /* Firefox */\n    display: -ms-flexbox;\n    /* IE10 */\n    display: -webkit-box;\n    /* Safari */\n    display: -webkit-flex;\n    /* Chrome, WebKit */\n    display: flexbox;\n    display: flex;\n}\n\n.break {\n    overflow: hidden;\n    text-overflow: ellipsis;\n    white-space: nowrap;\n}\n\n.user {\n    width: 100%;\n    margin: $gap*2 0;\n    padding: 0 $gap*2;\n    display: flex;\n    .tab {\n        display: inline-block;\n        width: $gap*8;\n        color: $color62;\n        border-radius: $gap;\n        background-color: $colore7;\n        margin-right: $gap*2;\n        text-align: center;\n        height: $gap*4;\n        line-height: $gap*4;\n        vertical-align: middle;\n    }\n    .good {\n        background-color: $color80;\n        color: $colorfff;\n    }\n    .title {\n        font-size: $gap*3.6;\n        font-weight: bold;\n        display: block;\n        width: 100%;\n        flex: 1;\n        @extend .break;\n    }\n    .head {\n        display: inline-block;\n        width: $gap*9;\n        height: $gap*9;\n        margin-right: $gap*2;\n        img {\n            width: $gap*9;\n            border: 2px solid #fff6e6;\n            border-radius: 50%;\n        }\n    }\n    .info {\n        overflow: hidden;\n        display: block;\n        width: 100%;\n        flex: 1;\n        &:after {\n            clear: both;\n        }\n        .t-title {\n            font-size: $gap*3.6;\n            font-weight: bold;\n            width: 100%;\n            color: #333;\n            @extend .break;\n        }\n        .mt12 {\n            margin-top: 12px;\n        }\n        .cl {\n            display: inline-block;\n            width: 68%;\n            .name {\n                color: $color62;\n            }\n            .mt10 {\n                margin-top: $gap*2;\n            }\n        }\n        .cr {\n            width: 30%;\n            display: inline-block;\n            text-align: right;\n            .name {\n                margin-top: $gap*2;\n                color: $color80;\n                font-size: $gap*2.4;\n            }\n        }\n    }\n}\n\n.reply_content {\n    padding: 0 $gap*3;\n    margin-bottom: $gap*3;\n    img {\n        width: auto\\9;\n        height: auto;\n        max-width: 100%;\n        vertical-align: middle;\n        border: 0;\n        -ms-interpolation-mode: bicubic;\n    }\n    p {\n        line-height: 1.3;\n    }\n}\n\n.no-data {\n    width: 100%;\n    height: 100%;\n    padding: 40% 0;\n    color: $colord4;\n    display: inline-block;\n    font-size: 18px;\n    text-align: center;\n}\n\n\n/*\n**   rework 分界线\n**   ------------\n*/\n\n\n/* 颜色 */\n\n$white: #ffffff;\n$light: #d5dbdb;\n$title: #2c3e50;\n$text: #34495e;\n\n/* 基础布局 */\n\n$padding: 15px;\n$border: 1px solid $light;\n$radius: 4px;\n$font-title: 16px;\n$font-content: 14px;\n$font-info: 12px;\n$font-tag: 10px;\n\n/* 综合布局 */\n\n.text-overflow {\n    white-space: nowrap;\n    text-overflow: ellipsis;\n    overflow: hidden;\n}\n\n.title {\n    color: $title;\n    font-size: $font-title;\n    line-height: 1.5;\n    @extend .text-overflow;\n}\n\n.tag {\n    padding: 5px 6px;\n    font-size: $font-tag;\n    font-weight: 400;\n    border-radius: $radius;\n    background-color: $colore7;\n    text-align: center;\n    vertical-align: middle;\n}\n\n.user-avatar {\n    display: block;\n    width: 40px;\n    height: 40px;\n    border-radius: 50%;\n    margin-right: 10px;\n    border: 1px solid #F3F3F3;\n}\n\n.week-alert {\n    position: fixed;\n    top: 0;\n    left: 0;\n    right: 0;\n    text-align: center;\n    background-color: rgba(75, 75, 75, 0.85);\n    color: #fff;\n    padding: 9px 20px;\n    font-size: 15px;\n    z-index: 999;\n    &.alert-show {\n        animation: show 2s;\n    }\n    @keyframes show {\n        0% {\n            transform: translateY(-100%);\n        }\n        20% {\n            transform: translateY(0);\n        }\n        50% {\n            transform: translateY(0);\n        }\n        80% {\n            transform: translateY(0);\n        }\n        100% {\n            transform: translateY(-100%);\n        }\n    }\n}\n","#brim-mask {\n    pointer-events: none;\n}\n\n#page {\n    padding-top: 44px;\n    background-color:#fff;\n    transition: all .3s ease;\n    overflow-x:hidden;\n}\n\n.show-menu {\n    transform: translateX($gap*40);\n}\n\n.page-cover {\n    position: fixed;\n    top: 0;\n    right: 0;\n    bottom: 0;\n    left: 0;\n    background: rgba(0, 0, 0, .4);\n    z-index: 7;\n}\n\n\n/*模块入口样式*/\n\n.posts-list {\n    background-color: $white;\n    li {\n        padding: 10px $padding;\n        border-bottom: $border;\n    }\n    h3 {\n        @extend .title;\n        &:before {\n            content: attr(title);\n            margin-right: 10px;\n            margin-top: -3px;\n            @extend .tag;\n            color: $white;\n        }\n        &.top:before {\n            background: #E74C3C;\n        }\n        &.ask:before {\n            background: #3498DB;\n        }\n        &.good:before {\n            background: #E67E22;\n        }\n        &.job:before {\n            background: #9B59B6;\n        }\n        &.share:before {\n            background: #1ABC9C;\n        }\n    }\n    .content {\n        padding-top: 10px;\n        display: flex;\n    }\n    .avatar {\n       @extend .user-avatar;\n    }\n    .info {\n        display: block;\n        width: 100%;\n        flex: 1;\n    }\n    p {\n        padding: 3px 0;\n        display: flex;\n        color: $text;\n        font-size: $font-info;\n\n        &:first-child {\n            font-size: $font-content;\n        }\n        .name, time:first-child {\n           display: block;\n           width: 100%;\n           flex: 1;\n        }\n        b {\n            color: #42b983;\n        }\n    }\n}\n","#hd {\n  border-bottom: 1px solid #e8e8e8;\n  &.fix-header {\n    width: 100%;\n    background-color: rgba(255, 255, 255, 0.95);\n    position: fixed;\n    top: 0;\n    left: 0;\n    transition: all .3s ease;\n    box-shadow: 0 0 4px rgba(0,0,0,0.25);\n    z-index: 6;\n  }\n  &.no-fix {\n    width: 100%;\n    background-color: #fff;\n    overflow: hidden;\n  }\n  &.show {\n    transform: translateX(200px);\n  }\n}\n.nv-toolbar {\n  width: 100%;\n  height: 44px;\n  display: flex;\n  align-items: center;\n\n\n  .toolbar-nav {\n    width: 49px;\n    height: 44px;\n    position: absolute;\n    background: url(\"../images/components/nav_icon.png\") no-repeat center center;\n    background-size: 19px 16px;\n    margin: 0;\n    z-index: 1;\n    top: 0;\n    left: 0;\n  }\n\n  &>span {\n    display: block;\n    text-align: center;\n    height: 100%;\n    line-height: 44px;\n    font-size: 16px;\n    width: 100%;\n    position: relative;\n    z-index: 0;\n  }\n  .num {\n    background-color: #80bd01;\n    color: #fff;\n    width: 20px;\n    height: 20px;\n    line-height: 20px;\n    vertical-align: middle;\n    text-align: center;\n    border-radius: 50%;\n    position: absolute;\n    right: 10px;\n    top: 10px;\n    z-index: 10;\n  }\n  .add-icon{\n    color: #42b983;\n    position: absolute;\n    right: 10px;\n    top: 10px;\n    z-index: 10;\n    padding: 5px 15px;\n    border-radius: 5px;\n  }\n}\n.scroll-hide{\n  height: 100%;\n  overflow: hidden;\n}\n",".about-info {\n    height: 100%;\n    padding: 50px 15px;\n    line-height: 1.5;\n    background: #f7f7f7;\n    display: flex;\n\n    dt {\n        @extend .title;\n        padding: 1em 0;\n        font-weight: bold;\n    }\n    dd {\n        padding-bottom: 15px;\n        font-size: $font-content;\n        border-bottom: $border;\n    }\n\n    a {\n        color: #42b983;\n    }\n\n}\n",".topic-title {\n    padding: $gap;\n    margin:$padding;\n    font-size: 18px;\n    color: $title;\n    line-height: 1.5;\n    background-color:$colorf0;\n    border-radius:$gap;\n}\n\n.author-info {\n    display: flex;\n    align-items: center;\n    padding: 0 $padding;\n    color: $text;\n    font-size: 12px;\n    .col {\n        display: block;\n        width: 100%;\n        flex: 1;\n    }\n    .avatar {\n        display: block;\n        width: 40px;\n        height: 40px;\n        margin-right: $padding;\n        border-radius: 50%;\n    }\n    .right{\n        text-align: right;\n    }\n    span, time {\n        display: block;\n        padding: 5px 0;\n    }\n    .tag {\n        @extend .tag;\n        color: #ffffff;\n        &.top {\n            background: #E74C3C;\n        }\n        &.ask {\n            background: #3498DB;\n        }\n        &.good {\n            background: #E67E22;\n        }\n        &.job {\n            background: #9B59B6;\n        }\n        &.share {\n            background: #1ABC9C;\n        }\n    }\n}\n.topic-content {\n    padding: $padding;\n    margin-top: $padding;\n    background: #ffffff;\n    border-bottom: solid 1px $colord4;\n    .from{\n        color:$red;\n    }\n}\n.topic-reply {\n    @extend .title;\n    padding: $padding;\n    border-bottom: solid 1px $colord4;\n    strong {\n        color: #42b983;\n    }\n}\n.reply_num {\n    margin-top: $gap*4;\n    background-color: $colore7;\n    padding: $gap*2 0 $gap*2 $gap*2;\n    border-top: solid 1px $colord4;\n    border-bottom: solid 1px $colord4;\n}\n.reply-list {\n    width: 100%;\n    margin-top: $gap*3;\n    ul {\n        width: 100%;\n        list-style: none;\n        padding-left: 0;\n        li {\n            width: 100%;\n            list-style: none;\n            border-bottom: solid 1px $colord4;\n            &:last-child {\n                border-bottom: none;\n            }\n            .uped {\n                color: $color80;\n            }\n            .icon {\n                font-size: 26px;\n            }\n            .from{\n                color:$red;\n            }\n            .language-javascript{\n                background-color:$colorf0;\n                overflow-x:auto;\n            }\n        }\n    }\n}\n\n/* 回复框样式 */\n\n.reply {\n    margin: 0